html {
    font-family: 'Roboto Slab', sans-serif;
    line-height: 1.5;
  
    color: #ebe7ef;
  
    font-synthesis: none;
    text-rendering: optimizeLegibility;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    -webkit-text-size-adjust: 100%;
  }
  
  body {
    background: radial-gradient(
        circle at top,
        rgba(70, 212, 241, 0.98),
        rgba(250, 176, 103, 0.87)
      )
     ;
    background-repeat: repeat;
    background-size: 100% 100%, 30% 30%, 100% 100%;
    min-height: 110rem;
  }
  
  header {
    text-align: center;
  }
  
  header img {
    width: 8rem;
    object-fit: contain;
    margin: 3rem auto 1rem auto;
    filter: drop-shadow(0 0 8px rgba(0, 0, 0, 0.4));
  }
  
  h1 {
    font-family: 'Caprasimo', cursive;
    font-size: 3rem;
    margin: 0 auto 3rem auto;
    color: #3f3b00;
  }
  #game-container {
    max-width: 45rem;
    margin: 3rem auto;
    padding: 2rem;
    border-radius: 6px;
    background: linear-gradient(#383624, #282617);
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.5);
    position: relative;
  }
  
  #players {
    list-style: none;
    padding: 0;
    margin: 1rem 0;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 2rem;
  }
  
  #players li {
    display: flex;
    align-items: center;
    width: 50%;
    border: 2px solid transparent;
  }
  #players.highlight-player li.active {
    border-color: #f6e35a;
    animation: pulse 2s infinite ease-in-out;
  }
  
  #players.highlight-player li.active .player-name,
  #players.highlight-player li.active .player-symbol {
    color: #f6e35a;
  }
  #edity{
    width: 3rem;
    border: none;
    background: none;
    color: #c3ba78;
    font-size: 0.9rem;
    cursor: pointer;
    transition: color 0.2s;
    padding: 0.5rem 0.25rem 0.25rem 0.25rem;
    text-align: center;
  }
  
  .player {
    border: 2px solid transparent;
    padding: 0.5rem;
    border-radius: 4px;
    font-weight: bold;
  }
  
  .player-name {
    display: inline-block;
    width: 10rem;
    font-size: 1rem;
    color: #e1dec7;
    text-transform: uppercase;
    margin: 0;
    padding: 0.5rem;
    border-radius: 4px;
    text-overflow: ellipsis;
    text-align: center;
  }
  
  .player input {
    font: inherit;
    font-size: 1rem;
    width: 10rem;
    border: none;
    padding: 0.5rem;
    animation: pulse-text 2s infinite;
    background-color: #46432f;
    text-align: center;
    text-transform: uppercase;
  }
  
  .player-symbol {
    margin-left: 1rem;
    font-size: 1rem;
    color: #e1dec7;
  }
  
  ol {
    list-style: none;
    margin: 0;
    padding: 0;
  }
  #red{
    width: 2rem;
    height: 2rem;
    border: none; 
    background:red;
    margin-right: 2rem;
    
    font-size: 5rem;
    cursor: pointer;
    font-family: 'Caprasimo', cursive;
    padding: 1rem;
  }
  #yellow{
    width: 2rem;
    height: 2rem;
    border: none;
    background: yellow;
    margin-right: 2rem;
    font-size: 5rem;
    cursor: pointer;
    font-family: 'Caprasimo', cursive;
    padding: 1rem;
  }
  #game-board {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 2rem;
    margin: 3rem 0;
    padding: 0;
    flex-direction: column;
  }
  
  #game-board ol {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 2rem;
    margin: 0;
    padding: 0;
  }
  
  #game-board button {
    width: 4rem;
    height: 4rem;
    border: none;
    background: #aca788;
    color: #3f3b00;
    font-size: 2rem;
    cursor: pointer;
    font-family: 'Caprasimo', cursive;
    padding: 1rem;
  }
  .restart{
   
      width: 3rem;
      border: none;
      background: none;
      color: #c3ba78;
      font-size: 0.9rem;
      cursor: pointer;
      transition: color 0.2s;
      padding: 0.5rem 0.25rem 0.25rem 0.25rem;
      text-align: center;
    }
    
  